const initSliderTable = () => {
  const sliderBlock = document.querySelector('.conditions');

  if (!sliderBlock) {
    return;
  }

  const slider = sliderBlock.querySelector('.swiper');
  const breakpoint = window.matchMedia('(min-width:768px)');
  const btn = sliderBlock.querySelectorAll('.table__btn');
  let swiper;

  const initSliderCard = () => {
    // eslint-disable-next-line
    swiper = new Swiper(slider, {
      slidesPerView: 1,
      grabCursor: true,

      keyboard: {
        enabled: true,
        onlyInViewport: true,
        pageUpDown: true,
      },
    });
  };

  const breakpointChecker = () => {
    if (breakpoint.matches) {
      if (slider.classList.contains('swiper-initialized')) {
        swiper.destroy(false, true);
      }
    } else {
      initSliderCard();

      btn.forEach((element, index) => {
        element.addEventListener('click', () => {
          swiper.slideTo(index, 0);
        });
      });

      swiper.on('slideChange', () => {
        btn.forEach((element, index) => {
          if (swiper.activeIndex === index) {
            element.classList.add('table__btn--curent');
          } else {
            element.classList.remove('table__btn--curent');
          }
        });
      });
    }
  };
  breakpoint.addListener(breakpointChecker);
  breakpointChecker();
};

export {initSliderTable};
