import {ScrollLock} from '../utils/scroll-lock';

const mainNav = document.querySelector('.main-nav');
const overlayNav = document.querySelector('.main-nav__overlay');
const toggleNav = document.querySelector('.main-nav__toggle');
const btnLightNav = document.querySelector('.btn--light.btn--main-nav');
const anchors = document.querySelectorAll('.main-nav__link');

const scrollLock = new ScrollLock();

const closesNav = () => {
  scrollLock.enableScrolling();
  mainNav.classList.remove('main-nav--open');
};

const closesBurgerMenu = (element) => {
  if (element) {
    element.addEventListener('click', () => {
      const blockID = element.getAttribute('href').substring(1);

      document.querySelector(`#${blockID}`).scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });

      closesNav();
    });
  }
};

const initBurgerNav = () => {
  for (const anchor of anchors) {
    closesBurgerMenu(anchor);
  }

  closesBurgerMenu(btnLightNav);

  if (toggleNav) {
    toggleNav.addEventListener('click', (evt) => {
      if (mainNav.classList.contains('main-nav--open')) {
        evt.preventDefault();
        closesNav();
      } else {
        scrollLock.disableScrolling();
        mainNav.classList.add('main-nav--open');
      }
    });
  }

  window.addEventListener('keydown', (evt) => {
    if (evt.keyCode === 27) {
      if (mainNav.classList.contains('main-nav--open')) {
        evt.preventDefault();
        closesNav();
      }
    }
  });

  if (overlayNav) {
    overlayNav.addEventListener('click', (evt) => {
      if (mainNav.classList.contains('main-nav--open')) {
        evt.preventDefault();
        closesNav();
      }
    });
  }
};


export {initBurgerNav};
