const initSlider = () => {
  const sliderBlock = document.querySelector('.cases');

  if (!sliderBlock) {
    return;
  }

  const slider = sliderBlock.querySelector('.swiper');
  const pagination = sliderBlock.querySelector('.swiper-pagination');
  const breakpoint = window.matchMedia('(min-width:768px)');
  let swiper;

  const initSliderCard = () => {
    // eslint-disable-next-line
    swiper = new Swiper(slider, {
      slidesPerView: 1,
      grabCursor: true,
      spaceBetween: 16,
      autoHeight: true,

      pagination: {
        el: pagination,
        type: 'bullets',
        clickable: true,
      },
    });
  };

  const breakpointChecker = () => {
    if (breakpoint.matches) {
      if (slider.classList.contains('swiper-initialized')) {
        swiper.destroy(false, true);
      }
    } else {
      initSliderCard();
    }
  };
  breakpoint.addListener(breakpointChecker);
  breakpointChecker();

};

export {initSlider};
